.card {
    background: #fff;
    border-radius: 2px;
    height: 158px;
    margin: 1rem;
    position: relative;
    width: 158px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  
  }
  
  .card > .img-container {
    height: 100%;
    
    overflow: hidden;
    
    background:#6CADDC;
  }
  .card > .img-container > img {
    width: 100%;
    
  }
   